body.register-page {
  background: linear-gradient(90deg, #e6f9ff 50%, rgba(255, 255, 255, 0) 100%);
}

body.register-page .container > .row {
  min-height: 100vh;
  padding-top: 70px;
  padding-bottom: 200px;
}

body.register-page.ylw {
  background: linear-gradient(90deg, #fffde5 0%, #ffffff 100%);
}

.register-page--header {
  min-height: 30vh;
}

.register-page--header:before {
  content: "";
  position: absolute;
  width: 800px;
  min-width: 50vw;
  height: 450px;
  background-image: url(../img/login-page--triangles.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.register-page label {
  color: #56CCF2;
  font-weight: normal;
  text-transform: uppercase;
  font-size: .7rem;
  letter-spacing: .01251em;
  margin-bottom: 0;
}

.register-page input, .register-page select, .register-page textarea {
  border-color: #d1e6ec;
  font-size: .9rem;
  color: #a8a8a8;
}

body.register-page.ylw .register-page--header:before {
  background-image: url(../../partnersspace/img/triangles-ylw.svg) !important;
}

body.register-page.ylw label {
  color: #dbb300 !important;
}

body.register-page.ylw .form-row--header span {
  color: grey;
  font-weight: 300;
}

body.register-page.ylw .form-control {
  border-color: lightgrey;
}

.form-row--header {
  padding: 5%;
  background: linear-gradient(-180deg, rgba(114, 47, 47, 0) 0%, white 100%);
  margin-bottom: 4%;
}

.form-row--header span {
  margin-top: 10%;
  text-transform: capitalize;
  font-family: Roboto Condensed, sans-serif;
  font-weight: lighter;
  font-size: 1.6rem;
  color: #00a2ff;
}
